section.header {
  padding-top: 70px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  @media screen and (max-width: $smDesktopWidth) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-top: 40px;
  }
  @media screen and (max-width: $tableWidth) {
    padding-top: 30px;
  }
  
  .logo {
    
  }
  
  .nav {
    @media screen and (max-width: $smDesktopWidth) {
      padding-top: 40px;
    }
    @media screen and (max-width: $tableWidth) {
      padding-top: 20px;
    }
    &-list {
      display: flex;
      flex-direction: row;
      &-item {
        position: relative;
        .nav-link {
          padding: 0 30px;
          font-size: 24px;
          font-weight: normal;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          color: #484848;
          @media screen and (max-width: $tableWidth) {
            font-size: 20px;
            padding: 0 24px;
          }
          @media screen and (max-width: $phoneWidth) {
            font-size: 16px;
            padding: 0 16px;
          }
        }
        &:after {
          content: "";
          display: block;
          position: absolute;
          top: 0;
          right: 0;
          width: 1.2px;
          height: 100%;
          background-color: #000000;
          z-index: 1;
        }
        &:last-child {
          padding-right: 0px;
          &:after {
            display: none;
          }
        }
      }
    }
  }
}