body {
	margin: 0 auto;
	height: 100vh;
	background-image: url("../img/background.png");
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	font-family: $mainFont;
}
  
.wrapper {
	max-width: 1300px;
	padding: 0 40px;
	margin: 0 auto;
	@media screen and (max-width: $tableWidth) {
		padding: 0 20px;
	}
}






.home-main-content {
  padding-top: 260px;
  @media screen and (max-width: $smDesktopWidth) {
    padding-top: 200px;
  }
  @media screen and (max-width: $tableWidth) {
    padding-top: 140px;
  }
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .title {
    text-align: center;
    font-size: 60px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 3px;
    color: #0d588e;
    @media screen and (max-width: $smDesktopWidth) {
      font-size: 52px;
    }
    @media screen and (max-width: $tableWidth) {
      font-size: 36px;
    }
  }
  .button {
    padding-top: 50px;
    width: 100px;
    height: 100px;
    @media screen and (max-width: $smDesktopWidth) {
      padding-top: 30px;
      width: 80px;
      height: 80px;
    }
    @media screen and (max-width: $tableWidth) {
      width: 60px;
      height: 60px;
    }
  }
}